.row {
  display: block;
  width: 100%; }

.styled-label {
  font-size: 1.5rem;
  display: inline-block;
  width: 20%;
  text-align: right;
  padding-right: 10px; }

.styled-input {
  border-radius: 3px;
  margin: 0 0 1em;
  width: 70%;
  padding: 5px; }
  .styled-input ::-webkit-input-placeholder {
    font-size: 0.7rem; }
  .styled-input ::-ms-input-placeholder {
    font-size: 0.7rem; }
  .styled-input ::placeholder {
    font-size: 0.7rem; }

.login-button {
  height: 90%;
  width: 15%;
  background-color: #696969;
  float: right;
  color: #fff;
  border: 0px solid transparent;
  cursor: pointer; }

